import '../css/List.css';
import { Link } from 'react-router-dom';

export default function List(){
    function scrollPos(){
        window.scrollTo(0,0);
    }
    return(
    <>
    <section id="listp">
        <div id="listtit">
            <h1>PROJECT<br/>LIST</h1>
        </div>
        <div id="listcon">
            <ul>
                <li>
                    <figure>
                        <video autoPlay loop muted>
                            <source src="img/project1video4.mp4" type="video/mp4"/>
                        </video>
                        <p>
                            <Link to="/Project" onClick={scrollPos}>Show Project</Link>
                        </p>
                    </figure>
                    <div>
                        <h2>Designhouse M+</h2>
                        <p>
                            Designhouse M+ 사이트 리뉴얼 프로젝트<br/>
                            심플하고 정돈된 레이아웃으로 편리하고,<br/>
                            다양한 정보를얻을 수 있도록 구상.
                        </p>
                    </div>
                </li>
                <li>
                    <figure>
                        <video autoPlay loop muted>
                            <source src="img/project2video2.mp4" type="video/mp4"/>
                        </video>
                        <p>
                            <Link to="/Project2" onClick={scrollPos}>Show Project</Link>
                        </p>
                    </figure>
                    <div>
                        <h2>Laneige</h2>
                        <p>
                            Laneige 사이트 클론 코딩 프로젝트<br/>
                            메인/서브페이지 2페이지 작업<br/>
                            세로 스크롤, 비디오로 다양한 기능 구현
                        </p>
                    </div>
                </li>
                <li>
                    <figure>
                        <video autoPlay loop muted>
                            <source src="img/project3video1.mp4" type="video/mp4"/>
                        </video>
                        <p>
                            <Link to="/Project3" onClick={scrollPos}>Show Project</Link>
                        </p>
                    </figure>
                    <div>
                        <h2>React Project</h2>
                        <p>
                            React Project<br/>
                            'Aesop'브랜드 제품을 React프로그램으로<br/>
                            간단한 사이트 제작
                        </p>
                    </div>
                </li>
                
            </ul>
        </div>
        <div  id="listmore">
            <span><a href="">MORE VIEW</a></span>
        </div>
    </section>
    </>
    );
}